.datetimepicker {
  --datetimepicker-z-index: 1000;
  --datetimepicker-bg: var(--ui-body-bg);
  --datetimepicker-border-width: var(--ui-border-width);
  --datetimepicker-border-color: var(--ui-border-color-translucent);
  z-index: var(--datetimepicker-z-index);
  display: block;
  width: 300px;
  padding: 0;
  margin: 0;
  color: var(--ui-body-color);
  text-align: left;
  user-select: none;
  list-style: none;
  background-color: var(--datetimepicker-bg);
  border: var(--datetimepicker-border-width) var(--ui-border-style) var(--datetimepicker-border-color);
}
.datetimepicker th {
  border-bottom: 0;
}
.datetimepicker th[abbr] {
  width: 14.2857142857%;
}
.datetimepicker tr:last-child > td {
  border-bottom: 0;
}
.datetimepicker.datetimepicker-disabled {
  pointer-events: none;
}

.datetimepicker-inline {
  --datetimepicker-bg: transparent;
  --datetimepicker-border-width: 0;
}

.datetimepicker-action {
  cursor: pointer;
}
.datetimepicker-action:hover {
  color: var(--ui-body-color);
  background-color: rgba(var(--ui-body-color-rgb), 0.1);
}
.datetimepicker-action:focus-visible {
  outline: 0;
  background-color: rgba(var(--ui-primary-rgb), 0.2);
}
.datetimepicker-action.datetimepicker-active {
  color: var(--ui-primary-contrast);
  background-color: var(--ui-primary);
}
.datetimepicker-action.datetimepicker-active:hover, .datetimepicker-action.datetimepicker-active:focus-visible {
  background-color: var(--ui-primary-emphasis);
}

.datetimepicker-disabled {
  cursor: not-allowed;
  opacity: 0.2;
}
.datetimepicker-disabled:focus-visible {
  outline: 0;
  background-color: rgba(var(--ui-primary-rgb), 0.2);
}

.datetimepicker-time {
  font-size: var(--ui-font-size-lg);
}

.datetimepicker-today {
  position: relative;
}
.datetimepicker-today::before {
  position: absolute;
  right: 4px;
  bottom: 4px;
  display: inline-block;
  content: "";
  border: solid transparent;
  border-width: 0 0 7px 7px;
  border-top-color: var(--ui-border-color-translucent);
  border-bottom-color: var(--ui-primary);
}
.datetimepicker-today.datetimepicker-active::before {
  border-bottom-color: var(--ui-primary-contrast);
}

@media (min-width: 768px) {
  .datetimepicker-wide {
    width: 600px;
  }
}
.datetimepicker-modal {
  --datetimepicker-border-width: 0;
  width: 100%;
}

